import Filters from "@/config/filters.js";

class Store {
  constructor() {
    this.state = {
      alloys: [],
      alloysBackup: [],
      selectedFilters: {},
      filtersList: JSON.parse(JSON.stringify(Filters)),
      productUniverse: "1 - Aerospace high performance alloys"
    };
  }
  filterAlloysItems() {
    let filters = this.state.selectedFilters;
    this.state.alloys = this.state.alloysBackup.filter(function(alloy) {
      for (let key in filters) {
        // Si le filtre est une checkbox, on vrifie que la valeur boolenne correspond bien  celle du filtre
        if (filters[key].type === "checkbox") {
          if (alloy[key] === undefined || alloy[key] != filters[key].value) {
            return false;
          }
        }
        // Si le filtre est un slider, on vrifie que la valeur soit situe dans les bornes de celles du filtres
        else if (filters[key].type === "slider") {
          if (
            alloy[key] === undefined ||
            parseFloat(alloy[key]) <= parseFloat(filters[key].value)
          ) {
            return false;
          }
        }
        // Si le filtre est un champ texte, on vrifie que le champ de l'alliage contienne le texte saisi dans le filtre
        else if (filters[key].type === "text") {
          if (
            alloy[key] === undefined ||
            !alloy[key].toLowerCase().includes(filters[key].value.toLowerCase())
          ) {
            return false;
          }
        }
        // Si le filtre est un select, on vrifie que la valeur de l'alliage soit gale  la valeur du select
        else if (filters[key].type === "select") {
          if (filters[key].value.length != 0 && alloy[key] != undefined) {
            let match = false;
            for (let val of filters[key].value) {
              if (
                Number.isInteger(val) &&
                parseInt(alloy[key]) === parseInt(val)
              ) {
                match = true;
              }
            }
            return match;
          } else {
            return true;
          }
        }
        // Si on utilise la recherche globale
        else if (filters[key].type === "globalSearch") {
          let includeText;
          // Pour chaque champ de l'alliage
          for (let field in alloy) {
            includeText = false;
            // Si la valeur du champ n'est pas indfinie et n'est pas un nombre
            if (alloy[field] != undefined && isNaN(alloy[field])) {
              // Et que sa valeur contient celle recherche
              if (
                alloy[field]
                  .toLowerCase()
                  .includes(filters[key].value.toLowerCase())
              )
                includeText = true;
            }
            // On arrte la boucle sur les champs d'alliage courant
            if (includeText) break;
          }
          // Si aucun champ n'inclu le terme recherch, on ne renvoie pas l'alliage en question
          if (!includeText) return false;
        }
      }
      return true;
    });
  }
  /**
   * Rinitialisation des filtres
   */
  resetFilters() {
    // Remise  zro des filtres slectionns
    this.state.selectedFilters = {};
    // Rinitialisation de l'tat des filtres
    this.state.filtersList = JSON.parse(JSON.stringify(Filters));
    // Rinitialisation du type de Univers
    this.state.productUniverse = "all";
    // On relance la mthode filtre pour faire apparatre tous les alliages
    this.filterAlloysItems();
    this.changeUniverse("1 - Aerospace high performance alloys");
  }
  /**
   * Supprime un filtre prcis, en lui passant sa clef en paramtre
   * (identifiant technique prsent en base)
   */
  resetSpecificFilter(key) {
    delete this.state.selectedFilters[key];
  }
  /**
   * Changement de l'objet contenant les filtres actif
   * @param {*} property Nom de la proprit sur lequel le filtre sera actif
   * @param {*} type Type de filtre ("checkbox", "slider", ou "text")
   * @param {*} value Valeur
   */
  addFilter(property, type, value) {
    /* Si la valeur est "false", on efface la proprit
      Ex: si on souhaite tous les alliages qui ont la proprit "Rods"  "true",
      on coche la checkbox et on rcupre tous les alliages qui ont cette proprit  "true".
      Par contre, si on dcoche la checkbox, celle-ci nous renvoie false. Or, nous ne souhaitons pas
      rcuprer tous les alliages qui ont la proprit "Rods"  "false", mais celles qui sont  "true" OU "false".
      On retire donc le filtre afin de tout rcuprer.
    */
    if (value === false || value === "" || value === "all") {
      delete this.state.selectedFilters[property];
    } else {
      this.state.selectedFilters[property] = { type, value };
    }
    this.filterAlloysItems();
  }
  changeUniverse(universName) {
    this.state.productUniverse = universName;
    this.addFilter("productUniverse", "text", universName);
  }
  sortAlloysItems(param) {
    // Tri sur un paramtre
    if (param) {
      this.state.alloys.sort((a, b) => {
        // Si c'est une chane de caractres
        if (isNaN(parseFloat(a[param]))) {
          return a[param] > b[param] ? 1 : -1;
        }
        // Si c'est un nombre
        else {
          return parseFloat(b[param]) - parseFloat(a[param]);
        }
      });
    }
    // Tri par dfaut
    else {
      this.state.alloys.sort((a, b) => {
        return a["brandName"] > b["brandName"] ? 1 : -1;
      });
    }
  }
}

export default new Store();
